.navbar {
  width: 100%;
  height: 100px;

  display: flex;
  flex-direction: row;
}
.Main
{
  background-image: url(../assets/Hero\ Image.png);
  width: 100%;
  height: 135vh;
  background-position: center;
  background-size: auto;
  background-color: black;
  background-repeat: no-repeat;

}
.navbar #text
{
  padding-left: 20px;
  height: 30px;
  width: 170px;
}

.textNav
{

  font-weight: 600;
  font-size: 60px;
  text-align: left;
  margin-top: 190px;
  width: 600px;
  margin-left: 100px;
  color: white;
  font-family:'Poppins', sans-serif;

}
.navButtons
{
  display: flex;
  margin: 30px;
  margin-left: 100px;
  
}
.navButtons button{
  border-radius: 30px;
  background: transparent;
  border-color: orange;
  border: 2px solid orange;
  color: white;
  padding: 15px;
}
.Navbut button{
  width: 300px;
}
.Navbut button :hover
{
  cursor: pointer;
}
#navbut1
{
  margin-left: 30px;
  
}
.navbar .leftSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 150px;
}

.navbar .leftSide img {
  width: 30px;
}

.navbar .rightSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar a {
  color: white;
  text-decoration: none;
  margin: 20px;
}



.navbar #open {
  padding-left: 0px;
}
.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar #open .hiddenLinks {
  display: inherit;
  margin-left: 30px;
}

.navbar #close .hiddenLinks {
  display: none;
}

.navbar #open a {
  width: 70px;
  margin: 5px;
}
@media only screen and (max-width: 900px) {
  .navbar .rightSide a {
    width: 70px;
  }
  .navbar .leftSide {
    padding-left: 50px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar .rightSide a {
    display: none;
  }

  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 50px;
  }

  .navbar .rightSide button {
    display: inherit;
  }
}

@media only screen and (min-width: 600px) {
  .navbar .rightSide button {
    display: none;
  }

  .hiddenLinks {
    display: none;
  }
}